    text-align: center;
}

.home button {
    margin: auto;
}

}

.navbar {
    width: 100%;
    height: 120px;
    position: absolute;
    z-index: 10;
    background: rgba(0, 0, 0, 0.4);
    border-bottom: .5px solid white;
}

.navbar .container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}

.navbar .icon {
  font-size: 1.4rem;
  color: #fff;
}

.navbar .logo .icon {
    font-size: 3rem;
    margin-right: 1rem;
    color: var(--primary-color);
}

.hamburger {
    display: none;
    padding: 1rem;
}

.navbar .nav-menu {
    height: 100%;
    align-items: center;
    display: flex;
    padding: 1rem;
}
.navbar li {
    width: -webkit-max-content;
    width: max-content;
    padding: 1rem;
    font-weight: 400;
    color: #fff;
}

  .menu-item .sub__menus li a {
    color: #000000 !important;
    font-weight: 400;
    line-height: 10px;
  }

  .menu-item .sub__menus li a:hover {
    color:var(--primary-color) !important;
  }


/* -----------------------------------------------------> SUB MENU */
.sub__menus {
    position: absolute;
    display: none;
    background: white;
    border: 1px solid lavender;
    border-radius: 10px;
    width: 280px;
    /* left: -17px; */
    padding: 2px 0 0 0;
    margin-top: 10px;
    z-index: 1000;
  }



  /* -----------------------------------------------------> DROPDOWN ARROW */
  .sub__menus__arrows {
    position: relative;
  }

  .sub__menus__arrows svg, .sub__menus__arrows__full svg {
    padding-top: 0px;
    position: absolute;
    top: 19px;
  }



  .sub__menus__arrows:hover .sub__menus{
    display: block;
  }

  .sub__menus__arrows__full:hover .sub__menus{
    display: block;
    z-index: 1000000;
  }

.menu-item .sub__menus  a {
  padding: 7px 0 0px 0;
  margin: 0px 5px;
}




@media screen and (max-width: 940px) {
    .navbar .hamburger {
        display: block;
        z-index: 15;
    }

    .navbar .nav-menu {
        position: absolute;
        flex-direction: column;
        width: 100%;
        height: 80vh;
        top: 0;
        left:-100%;
        align-items: center;
        justify-content: center;
        background: #7F669D;
        transition: 0.5s ease-in;
    }

    .navbar .active {
        left: 0;
    }

    .navbar li {
        margin-right: 1rem;
        font-size: 2rem;
    }

    .navbar button {
        font-size: 1.8rem;
    }

    .menu-item .sub__menus li a {
        font-size: 14px;
      }

}


.footer {
    width: 100%;
    height: 55vh;
    position: relative;
    background: rgba(0, 0, 0, 0.7);
}

.footer:before {
    content:'';
    position: absolute;
    background: url(/helping-hands/static/media/footer-bg.824ca03e.jpg)no-repeat center center/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
}

.footer .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.logo-footer img{
    width: 20%;
}

.footer .top {
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: space-between;
}

.footer .top .logo-footer {
    display: flex;
    align-items: center;
}

.footer .top .icon {
    font-size: 3rem;
    margin-right: 1rem;
    color: var(--primary-color);
    cursor: pointer;
}

.footer h2 {
    font-size: 2rem;
}


.footer .col-container {
    display: grid;
    grid-template-columns: 1fr 1fr 2fr;
    padding: 1rem;
    width: 100%;
}

.footer h3 {
    margin: 1rem 0 2rem 0;
    text-transform: uppercase;
}


.footer p {
    margin: .5rem 0;
}

.footer form {
    width: 100%;
    margin-bottom: .6rem;
    position: relative;
}

.footer form input {
    padding: 1rem;
    width: 100%;
    outline: none;
    font-size: 1rem;
    border: 1px solid #aaa;
    border-radius: 4px;
    color: #333;
}

.footer form input:focus {
    color: #333;
    outline: var(--primary-color);
    box-shadow: 0 0 8px 0 var(--primary-dark);
}

.footer form .mail-icon {
    position: absolute;
    font-size: 1.4rem;
    margin: 1rem;
    right: .2rem;
    color: #aaa;
    cursor: pointer;
}

.footer form .social-icon {
    margin: 1rem 1rem 1rem 0;
    padding: .6rem;
    background-color: var(--primary-dark);
    border-radius: 4px;
    color: #f8f8f8;
    font-size: 3rem;
}


@media screen and (max-width: 940px) {
    .footer {
        height: 100%;
    }

    .footer .col-container {
        grid-template-columns: 2fr 2fr;
    }

    .footer form {
        grid-column: 1 / span 2;
    }

    .logo-footer img{
        width: 50%;
    }
}



